import React, {Component} from 'react';

class DateSuggestions extends Component {



    render() {
        return (
            <div>
                {this.props.suggestions.map((item) => {
                    return <button onClick={this.props.onSelect("date")} value={item}>{item}</button>
                })}
            </div>
        );
    }
}

export default DateSuggestions;
