import React, {Component} from 'react';
import DateSuggestions from "./DateSuggestions";

export class FormDate extends Component {
    state={
        suggestions: []
    };

    onInputChange = e =>{
       let result=[];

        for (let i = 0; i < 5; i++) {
            result.push(`${Math.floor(Math.random() * 24)}:${Math.floor(Math.random() * 61)}`)
        }
        this.setState({suggestions : result})
    };

    continue = e => {
        e.preventDefault();
        this.props.nextStep();
    };

    back = e => {
        e.preventDefault();
        this.props.prevStep();
    };

    render() {
         const {values, handleChange} = this.props;

        return (
            <div className="date-container">
                <div className="input-container">
                    <input type="date" onChange={this.onInputChange}/>
                    <input type="time" onChange={this.onInputChange}/>
                </div>
                <p className="choose-date-title">Choose the time that suits you</p>
                <div className="suggestion-container">
                    <DateSuggestions onSelect={handleChange} suggestions={this.state.suggestions}/>
                </div>

                <div className="date-button-container">
                    <button className="neuro-btn" onClick={this.continue}>Continue</button>
                    <button className="neuro-btn" onClick={this.back}>Back</button>
                </div>
            </div>
        );
    }
}
